form {
	display: flex;
	flex-direction: column;
	gap: 1rem;
}

form input {
	font-size: 18pt;
	height: 42px;
	width: 300px;
}

.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}
.warning {
	color: red;
}

.game-over {
	position: absolute;
	z-index: 900;
	height: 100vh;
	width: 100vw;
	top: 0;
	background-color: #1c1c1c;
	opacity: 95%;
}

.inner-content {
	display: flex;
	flex-direction: column;
	color: azure;
	position: relative;
	z-index: 1001;
	height: inherit;
	justify-content: center;
	align-items: center;
	font-size: 1.4rem;
}
.inner-content p span {
	color: red;
}
.inner-content button {
	width: fit-content;
}

.word-container {
	display: flex;
}

.letter-container {
	display: inline-block;
	margin-right: 10px;
}

.letter-box {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 30px;
	height: 30px;
	border: 2px solid lightgreen;
	font-size: 20px;
	font-weight: bold;
}
